import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {LayoutComponent} from './layout/layout/layout.component';

import {LayoutSessionComponent} from './layoutsession/layout/layoutsession.component';
import {CreateRegisterComponent} from 'src/client/app/modules/user/create-user/create-user';
import {PcPlanComponent} from './modules/plan/pcplan/pcplan.component';

const routes: Routes = [
  {path: '', component: LayoutComponent, loadChildren: './modules/pages.module#PagesModule'},
  {path: 'user/create', component: CreateRegisterComponent},
  {path: 'profile/home', component: LayoutSessionComponent, loadChildren: './modules/pagesprofile.module#PagesProfileModule'},
  {path: 'products', component: LayoutSessionComponent, loadChildren: './modules/pagesplan.module#PagesPlanModule'},
  {
    path: 'gestorconsulta',
    component: LayoutSessionComponent,
    loadChildren: './modules/pagesgestorconsulta.module#PagesGestorConsultaModule'
  },
  {
    path:'payu',
    component:LayoutSessionComponent,
    loadChildren: './modules/pagespayu.module#PagesPayUModule'
  }


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
