import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { HeaderSessionComponent } from './header/headersession.component';
import { FooterSessionComponent } from './footer/footersession.component';
import { LayoutSessionComponent } from './layout/layoutsession.component';
import { ScrollToModule } from '@nicky-lenaers/ngx-scroll-to';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { ScrollSpyModule } from 'ngx-scrollspy';

import {ExampleService} from "../../app/services/example/example.service";
import {ExampleProvider} from "../../app/services/example/example.provider";

import {ProfileBasicProvider} from "../../app/services/profile/profilebasic.provider";
import {ProfileBasicService} from "../../app/services/profile/profilebasic.service";

@NgModule({
  providers:[
    ExampleService,
    new ExampleProvider(),
    ProfileBasicService,
    new ProfileBasicProvider()
  ],
  declarations: [HeaderSessionComponent, FooterSessionComponent, LayoutSessionComponent],
  imports: [
    BrowserModule,
    CommonModule,
    RouterModule,
    NgbModule,
    ScrollSpyModule.forRoot(),
    ScrollToModule.forRoot()
  ]
})
export class LayoutSessionModule { }
