import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexProfileComponent } from '../profile/index/indexprofile.component';
import { IndexEditComponent } from '../profile/indexedit/indexedit.component';


const routes: Routes = [
  { path: '', component: IndexProfileComponent },
  { path:'edit', component : IndexEditComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class ProfileRoutingModule { }