import { Component, OnInit } from '@angular/core';
import { CommunicationService } from "../../../services/Communication.service";
import { Example } from "../../../../../domain/models/example/RootObject";

@Component({
  selector: 'app-indicadorriesgo',
  templateUrl: './indicadorriesgo.component.html',
  styleUrls: ['./indicadorriesgo.component.scss']
})
export class IndicadorRiesgoComponent implements OnInit {

  constructor(public communication: CommunicationService) { }

  public valorOBS: string;
	ngOnInit() {
		this.communication.obs_updateTransaction.subscribe(
			(resp: Example) => this.mapUpdateTransaction(resp),
			(errno) => console.log('error obs !')
		);
	}
	mapUpdateTransaction(response: Example) {
		this.valorOBS = response.title;
	}

}
